import {Component, OnInit, AfterViewInit} from '@angular/core';
import { SubDomain } from '../../../shared/interfaces/subdomain.interface';
import { SubDomainService,CompanyService} from '../../../shared/services/index';
import { Router} from '@angular/router';
import { PreviewComponent } from '../../templates/templateAll/preview.component';
import { DefaultJSON } from '../../templates/services/DefaultJSON.service';
import { App } from '../../+builder/models/model';

declare var jQuery:any;
declare var ga:any;
declare var _kmq:any;

@Component({
  selector: 'og-templates',
  template: require('./templates.component.html'),
  styles: [require('./templates.component.css')],
})

export class TemplatesComponent implements OnInit, AfterViewInit {
  subDomain: SubDomain;
  company_id: String;
  username: String;
  tempname:string;
  apps : any;
  templates: any[] = [];
  loader: Number = 0;
  appJson: App;

  constructor(private subDomainService: SubDomainService,
              private companyService: CompanyService,
              public _router:Router,
              private _defaultJson: DefaultJSON) {
    this.subDomain = subDomainService.subDomain;
    this.company_id = this.subDomain.company_id;
  }

  ngAfterViewInit() {
    this.companyService.getTemplates()
      .subscribe(
        (response: any[]) => {
          this.templates = response;
          this.loader = 1;
          setTimeout(()=> {this.animation();}, 500 );
        },
        (error: any) => {
          console.log(error);
        }
      );
  }
  animation(){
    var animations    = ['leftFade', 'topFade', 'rightFade', 'bottomFade'];
    var total_anim    = animations.length;
    var easeType      = 'swing';
    var animSpeed     = 1000;
    var hs_container  = jQuery('.hs_container');
    var hs_areas      = hs_container.find('.hs_area');
    var hs_images     = hs_container.find('img');
    var total_images  = hs_images.length;
    var cnt           = 0;
    hs_images.each(function(){
      var that = jQuery(this);
      jQuery('<img/>').load(function(){
        ++cnt;
        if(cnt === total_images) {
          hs_areas.each(function(){
            var area     = jQuery(this);
            var onImage = false;
            area.data('over',true).bind('mouseenter',function(){
              onImage = true;
              if(area.data('over')) {
                area.data('over',false);
                var total    = area.children().length;
                var current   = area.find('img:visible');
                var idx_current = current.index();
                var next    = (idx_current === total-1) ? area.children(':first') : current.next();
                next.show();
                var anim    = animations.shift();
                animations.push(anim);
                switch(anim) {
                  case 'rightFade':
                    current.animate({
                        'left':current.width()+'px',
                        'opacity':'0'
                      },
                      animSpeed,
                      easeType,
                      function(){
                        current.hide().css({
                          'z-index'  : '1',
                          'left'    : '0px',
                          'opacity'  : '1'
                        });
                        next.css('z-index','9');
                        area.data('over',true);
                        if(onImage) {
                          area.trigger('mouseenter');
                        }
                      });
                    break;
                  case 'leftFade':
                    current.animate({
                        'left':-current.width()+'px','opacity':'0'
                      },
                      animSpeed,
                      easeType,
                      function(){
                        current.hide().css({
                          'z-index'  : '1',
                          'left'    : '0px',
                          'opacity'  : '1'
                        });
                        next.css('z-index','9');
                        area.data('over',true);
                        if(onImage) {
                          area.trigger('mouseenter');
                        }
                      });
                    break;
                  case 'topFade':
                    current.animate({
                        'top':-current.height()+'px',
                        'opacity':'0'
                      },
                      animSpeed,
                      easeType,
                      function(){
                        current.hide().css({
                          'z-index'  : '1',
                          'top'    : '0px',
                          'opacity'  : '1'
                        });
                        next.css('z-index','9');
                        area.data('over',true);
                        if(onImage) {
                          area.trigger('mouseenter');
                        }
                      });
                    break;
                  case 'bottomFade':
                    current.animate({
                        'top':current.height()+'px',
                        'opacity':'0'
                      },
                      animSpeed,
                      easeType,
                      function(){
                        current.hide().css({
                          'z-index'  : '1',
                          'top'    : '0px',
                          'opacity'  : '1'
                        });
                        next.css('z-index','9');
                        area.data('over',true);
                        if(onImage) {
                          area.trigger('mouseenter');
                        }
                      });
                    break;
                  default:
                    current.animate({
                        'left':-current.width()+'px'
                      },
                      animSpeed,
                      easeType,
                      function(){
                        current.hide().css({
                          'z-index'  : '1',
                          'left'    : '0px'
                        });
                        next.css('z-index','9');
                        area.data('over',true);
                        if(onImage) {
                          area.trigger('mouseenter');
                        }
                      });
                    break;
                }
              }
            });
            area.bind('mouseleave', function(){
              onImage = false;
            });
          });
        }
      }).attr('src',that.attr('src'));
    });
  }
  ngOnInit() {
    //
  }

  selectTemplate(selector: any, $event: any) {
    var button = jQuery($event.target);
    button.html('Initializing');
    button.addClass('loading');
    button.attr('disabled', true);
    localStorage.setItem('temp_name', selector);
    this._router.navigate(['/builder']);
    /*---- Tracking code goes here ----*/
    ga('markettingteam.send', 'event', 'Builder', 'Click', 'UseTemplate');
    _kmq.push(['record', 'Builder Use Template Click', {'Template':selector}]);
    /*---------------------------------*/
  }

  onPreview(template: string) {
    this.tempname = template;
    let app = this._defaultJson.getJSON(template);
    app.setTemplateName(template);
    this.appJson = app;
    localStorage.setItem('template',JSON.stringify(this.appJson));
    /*---- Tracking code goes here ----*/
    ga('markettingteam.send', 'event', 'Builder', 'Click', 'PreviewTemplate');
    _kmq.push(['record', 'Builder Preview Template Click', {'Template':template}]);
    /*---------------------------------*/
  }

  getAvailableTemplates(): any[] {
    return this.templates.filter((template: any)=>template.available);
  }

  getNotAvailableTemplates(): any[] {
    return this.templates.filter((template: any)=>!template.available);
  }
}



